<template>
  <div>
        <div style="height:100px;">

    </div>
        <div class="row">
          <div class="col-12">
            <div class="p-2 shadow-lg  bg-white">
                <p class="text-center text-2xl font-bold">HISTORY PEMBELIAN<span class="typcn typcn-shopping-cart"></span></p>
                <hr class="style13 mt-2 mb-2">
                <div v-for="(item, index) in list" :key="index" class="p-2">
                    <hr class="style3"> 
                    <p class="font-bold ml-4">
                    {{format(item.tanggal.toDate())}}
                    </p>
                  <div  v-for="(item, index) in item.keranjang" :key="index+'hello'" class="shadow-lg p-3 border-b-2 border-blue-700 rounded-lg" style="background: rgb(255,255,255);
background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(247,247,251,1) 35%, rgba(195,227,233,1) 100%);"  >
                        <img :src="item.gambar.length > 10 ? item.gambar :require('@/static/nofound2.png')" class="img-thumbnail float-left mr-2" v-viewer alt="" style="height:50px;width:50px;">
                            <p class="pl-2 text-sm font-bold">{{item.nama_makanan}}</p>
                            <p class="pl-2 text-sm font-bold">Rp.{{item.harga}}</p>
                            <p class="pl-2 text-sm font-bold ">Total : {{item.total}}</p>
                </div>
                <!-- <tr v-for="(item, index2) in item.keranjang" :key="index2+'hello'" class="shadow-lg p-2" v-if="item.total>0">
                    <td class="font-bold">{{index+1}}.</td>
                    <td class="pl-2 font-bold">{{item.nama_makanan}}</td>
                    <td class="pl-2 font-bold">Rp. {{item.harga}}</td>
                    <td class="pl-2 font-bold">Total : {{item.total}}</td>
                </tr> -->
                </div>
                
                <br>
                <!-- <button type="button" @click="kirim()" class="btn btn-sm btn-style9 ">Order!</button> -->
                <br>
                <hr class="style9 mt-2 mb-2">
            </div>
          </div>
      </div>
  </div>
</template>
<script>
var id = require("date-fns/locale/id");
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
// let rdb = firebase.database()
import autophp from '@/plugins/autophp';
let sdb = new autophp();
export default{
  data(){
    return{
        datanya:{},
        foods:[
            {
                img:"@/static/gambar1.jpg"
            }
        ],
    }
  },
  computed:{
      list(){
          let data = this.datanya 
        data= _.orderBy(data, ['tanggal'],['desc']); // Use Lodash to sort array by 'name'
        return data
      }
  },
  methods:{
        format(tanggal) {
      return this.$datefns.format(
        new Date(tanggal),
        "dddd, DD MMMM YYYY :HH:mm",
        {
          locale: id
        }
      );
    },
      getData(){
          db.collection(`users/${this.$store.state.users.uid}/keranjang`).onSnapshot(res=>{
              let data = res.docs.map(e=>{
                  return {
                      id:e.id,
                      ...e.data()
                  }
              })
              console.log(data)
              this.datanya = data
          })
      },
      kirim(){
          if(confirm("Apakah sudah yakin dengan orderan anda ?")){
              this.$store.state.cart = this.$store.state.cart.filter(e=>{
                  if(e.total>0){
                      return e
                  }
              })
                this.$store.state.cart.forEach(e=>{
                    db.collection(`food`).doc(e.id).get().then(res=>{
                        let data = res.data()
                        data.jumlah= data.jumlah - e.total
                        console.log(data)
                        db.collection(`food`).doc(e.id).set(data,{merge:true}).then(res=>{
                            console.log(e.nama_makanan+' Updated')
                        })
                    })                 
                })
                db.collection(`users/${this.$store.state.users.uid}/keranjang`).doc().set(this.$store.state.cart,{merge:true}).then(res=>{
                    alert("Berhasil dimasukkan ke keranjang")
                })
            }
      },
  },
  mounted() {
      setTimeout(() => {
          this.getData()

      }, 3000);
  },
  layout:"admin"
}
</script>